<template>
    <div class="add">
      <el-form  :model="formData" label-width="80px">
        <el-form-item label="描述">
          <el-input v-model="formData.title"></el-input>
        </el-form-item>
        <el-form-item label="内容">
          <editor v-model="formData.desc" :isChange="editorInit"></editor>  <!--局部注册的组件在这里使用-->
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submit">
            添加
          </el-button>
        </el-form-item>
      </el-form>
    </div>
</template>
<script>
  import editor from '../../components/wangeditor.vue'
    export default{
      name: "add",
      components:{
        editor
      },
      data(){
          return{
            formData:{
              title:'',
              desc:'',
            },
            editorInit: false
          }
      },
      methods:{
        submit(){
          this.$nextTick(() => {
            this.editorInit = true;
          });
          this.$axios.post('addReview',this.formData).then( res => {
              console.log(res.data)
            if(res.code == 200){
              this.$message({
                type: 'success',
                message: '添加成功!'
              });
              setTimeout(() => {
                this.$router.push('/review')
              },1000)
            }else{
              this.$message({
                type: 'error',
                message: '添加失败!'
              });
            }
          }).catch( () => {
            this.$message({
              type: 'warning',
              message: '已取消添加'
            });
          })
        }
      },
      created(){

      }
    }
</script>
<style scope>
  .add{
    width: 100%;
    height:100%;
    margin-right: 22px;
    padding-left:11px;
    padding-top: 20px;
  }
</style>



